.logo {
    display: inline-block;
    
    a {
        background-color: $color-logo-background;
        border-radius: 6px;
        font-size: 2.5rem;
        font-family: $font-alt;
        font-weight: 400;  
        letter-spacing: 2px;
        padding: 5px 10px;
        color: $global-color;
        text-decoration: none;
    }
}

.mobile-menu .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 28px 20px;
    position: relative;
    user-select: none;
}

.mobile-menu .menu-icon .navicon {
    background-color: #333;
    display: block;
    height: 2px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px;
}

.mobile-menu .menu-icon .navicon::before,
.mobile-menu .menu-icon .navicon::after {
    background-color: #333;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    transition: all .2s ease-out;
}

.mobile-menu .menu-icon .navicon:before {
    top: 5px;
}

.mobule-menu .menu-icon .navicon:after {
    top: -5px;
}

/* menu btn */
  
.mobile-menu .menu-btn {
    display: none;
}

.mobile-menu .menu-btn:checked ~ .menu {
    max-height: 240px;
}

  
